<template>
  <div>
    <meta
      name="viewport"
      content="width=device-width,initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"
    />
    <div class="" style="margin-top: 2.75rem" v-show="showInfo">
      <div class="sureCellGroup">
        <van-cell-group class="sureCellGroup" inset>
          <van-cell title="姓名" :value="dataList.real_name" />
          <van-cell
            title="证件类型"
            :value="dataList.card_type_text"
            v-if="getRoute !== 'truckDeclaration'"
          />
          <van-cell title="证件号" :value="dataList.id_card" />
          <van-cell title="手机号" :value="dataList.phone" />
          <van-cell
            title="车牌号"
            :value="dataList.plate_number"
            v-if="getRoute === 'truckDeclaration'"
          />
          <van-cell
            title="来源地"
            title-class="titleClass"
            v-if="getRoute === 'declarationBayonet'"
            :value="addAdress"
          >
          </van-cell>
          <van-cell
            title="来源地"
            title-class="titleClass"
            v-if="
              getRoute === 'zizhushengbaoWeixian' ||
              getRoute === 'goAndBackYiwu'
            "
            style="padding-right: 1rem"
            :value="addAdress"
          >
          </van-cell>
          <van-cell
            title="目的地"
            v-if="getRoute === 'risingFalling'"
            :value="addAdress"
          >
            <template #default>
              <van-field
                v-model="addAdress"
                input-align="right"
                readonly
                rows="1"
                autosize
                style="text-align: right"
                type="textarea"
              />
            </template>
          </van-cell>
          <van-cell
            title="来义后居所"
            v-if="
              getRoute !== 'risingFalling' &&
              getRoute !== 'shangyu' &&
              getRoute !== 'truckDeclaration'
            "
            :value="dataList_text.yw_street"
          />
          <van-cell
            title="来义时间"
            v-if="
              getRoute === 'zizhushengbaoWeixian' ||
              getRoute === 'goAndBackYiwu'
            "
            :value="dataList.arrival_time"
          />
          <van-cell
            title="离义时间"
            v-if="getRoute === 'risingFalling'"
            :value="dataList.leave_time"
          />
          <van-cell
            title="预计返义时间"
            v-if="getRoute === 'risingFalling'"
            :value="dataList.expect_return_time"
          />
          <van-cell
            title="离开中高风险地区时间"
            v-if="getRoute === 'zizhushengbaoWeixian'"
            :value="dataList.leave_riskarea_time"
          />
          <van-cell
            title="来义交通工具"
            v-if="getRoute === 'zizhushengbaoWeixian'"
            :value="dataList_text.arrival_transport_mode"
          />
          <van-cell
            title="车次/航班/车牌"
            v-if="getRoute === 'zizhushengbaoWeixian'"
            :value="dataList.arrival_sign"
          />
          <van-cell
            title="详细地址"
            v-if="
              getRoute === 'zizhushengbaoWeixian' ||
              getRoute === 'goAndBackYiwu'
            "
            style="padding-right: 0"
            :value="dataList.address"
          >
            <template #default>
              <van-field
                input-align="right"
                v-model="dataList.address"
                rows="1"
                readonly
                autosize
                type="textarea"
              />
            </template>
          </van-cell>
          <van-cell
            title="行程码"
            v-if="
              getRoute !== 'risingFalling' &&
              getRoute !== 'declarationBayonet' &&
              getRoute !== 'shangyu' &&
              getRoute !== 'truckDeclaration'
            "
          >
            <template #default>
              <img
                fit="cover"
                style="width: 5rem; height: 5rem"
                v-if="fileList[0] && fileList[0].status === 'done'"
                :src="fileList[0].content"
              />
            </template>
          </van-cell>
        </van-cell-group>
      </div>
      <van-checkbox
        v-model="checked"
        @change="changeCheckBox"
        class="checkBox"
        icon-size="16px"
      >
        <div
          style="
            color: red;
            text-align: center;
            height: 32px;
            display: flex;
            align-items: center;
            margin-top: 5px;
          "
        >
          本人承诺所填写的资料真实有效
        </div>
      </van-checkbox>
      <div class="submitBox">
        <van-button class="submitBoxBtn submitBoxBtn_left" @click="goBack"
          >返回修改</van-button
        >
        <van-button
          class="submitBoxBtn submitBoxBtn_right"
          v-if="checked"
          :disabled="isDisabled"
          :loading="isDisabled"
          loading-type="spinner"
          @click="onSubmit"
          loading-text="提交中..."
          >确定提交</van-button
        >
        <van-button
          class="submitBoxBtn submitBoxBtn_right"
          v-else
          :disabled="!checked"
          :loading="isDisabled"
          loading-type="spinner"
          @click="onSubmit"
          loading-text="提交中..."
          >确定提交</van-button
        >
      </div>
    </div>
    <van-popup
      v-model="showDiaglo"
      class="vanPopupIt"
      @close="back"
      lock-scroll
      :close-on-click-overlay="false"
      closeable
      close-icon-position="top-right"
      get-container="#app"
    >
      <div class="borderImage" v-if="getRoute !== 'truckDeclaration'">
        <div class="showQrTk">
          <van-image
            width="80"
            height="80"
            class="van_Image"
            :src="require('../assets/img/tj.png')"
          />
          <div style="font-size: 1.125rem; color: #333333; font-weight: bold">
            申报有效时间：<font style="color: red">{{ successInfoDjs }}</font>
          </div>
          <div class="lineHenxian"></div>

          <div class="content">
            <!-- <div>
              申报有效时间：<font style="color: red">{{ successInfoDjs }}</font>
            </div> -->
            <div>
              您好：<font style="color: blue">{{
                localSuccessInfo.real_name
              }}</font>
              <van-button
                plain
                icon="replay"
                style="left: 32%"
                type="info"
                @click="refersh"
                :loading="loadingRefersh"
                v-if="!disabledRefresh"
                loading-text="刷新"
                size="mini"
                >刷新</van-button
              >
              <van-button
                plain
                icon="replay"
                style="left: 28%"
                type="default"
                v-else
                @click="refersh"
                :loading="loadingRefersh"
                :disabled="disabledRefresh"
                loading-text="刷新"
                size="mini"
                >已刷新</van-button
              >
            </div>
            <div>
              最近申报:
              <b style="color: blue">{{ localSuccessInfo.last_datetime }}</b>
            </div>
            <div v-if="localSuccessInfo.jkm.indexOf('绿') > -1">
              健康码：<font style="color: green">{{
                localSuccessInfo.jkm
              }}</font>
            </div>
            <div v-if="localSuccessInfo.jkm.indexOf('黄') > -1">
              健康码：<font style="color: yellow">{{
                localSuccessInfo.jkm
              }}</font>
            </div>
            <div v-if="localSuccessInfo.jkm.indexOf('红') > -1">
              健康码：<font style="color: red">{{ localSuccessInfo.jkm }}</font>
            </div>
            <div>省内24小时核酸检测：{{ localSuccessInfo.hsjc_24_info }}</div>
            <template v-if="localSuccessInfo.hsjc_24_inf == '有'">
              <div>检测结果：{{ localSuccessInfo.hsjc_info.hsjc_result }}</div>
              <div>检测时间：{{ localSuccessInfo.hsjc_info.hsjc_time }}</div>
            </template>
          </div>
          <div class="content" v-if="getRoute === 'risingFalling'">
            感谢您的申请，祝您出行愉快，请在出行期间做好个人防护，避免去中高风险地区，返义后请及时申报。
          </div>
          <div class="content" v-else>
            欢迎来到世界小商品之都义乌！
            温馨提示：请您抵义后2小时内在全程做好个人防护的情况下及时主动向所在社区和单位（或所住酒店）报告，并配合做好信息核查、健康管理等工作。如有疑问，请拨打24小时值班电话0579-85532860、0579-85539123。疫情防控，需要您我共同努力！
          </div>
        </div>
      </div>
      <div class="borderImage" v-if="getRoute === 'truckDeclaration'">
        <div class="showQrTk">
          <van-image
            width="80"
            height="80"
            class="van_Image"
            :src="require('../assets/img/tj.png')"
          />
          <div style="font-size: 1.125rem; color: #333333; font-weight: bold">
            申报有效时间：<font style="color: red">{{ successInfoDjs }}</font>
          </div>
          <div class="lineHenxian"></div>
          <div class="content">
            <van-loading
              size="24px"
              vertical
              v-if="!localSuccessInfo_car.api_result"
              color="#0094ff"
              >加载中...</van-loading
            >
            <template v-else>
              <div>
                车牌:
                <b style="color: blue">{{
                  localSuccessInfo_car.plate_number
                }}</b>
              </div>
              <div>
                途经城市：<font style="color: green">{{
                  localSuccessInfo_car.travel_route
                }}</font>
              </div>
              <div>
                是否有经过疫区：<font style="color: red">{{
                  localSuccessInfo_car.is_warning_text
                }}</font>
              </div>
              <template>
                <div
                  v-for="(item, index) in localSuccessInfo_car.travel_data"
                  :key="item.city_conuty"
                >
                  <div style="margin-top: 1rem">
                    城市：<font style="color: red">{{ item.city_conuty }}</font>
                  </div>
                  <div>
                    地址：<font style="color: red">{{ item.addr }}</font>
                  </div>
                  <div>
                    <span style="text-indent: 0">开始时间：</span
                    ><font style="color: red">{{ item.beginGpsTime }}</font>
                  </div>
                  <div>
                    结束时间：<font style="color: red">{{
                      item.endGpsTime
                    }}</font>
                  </div>
                  <div class="lineHenxian"></div>
                </div>
              </template>
            </template>
          </div>
          <div class="content" v-if="getRoute === 'risingFalling'">
            感谢您的申请，祝您出行愉快，请在出行期间做好个人防护，避免去中高风险地区，返义后请及时申报。
          </div>
          <div class="content" v-else>
            欢迎来到世界小商品之都义乌！
            温馨提示：请您抵义后2小时内在全程做好个人防护的情况下及时主动向所在社区和单位（或所住酒店）报告，并配合做好信息核查、健康管理等工作。如有疑问，请拨打24小时值班电话0579-85532860、0579-85539123。疫情防控，需要您我共同努力！
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Debounce } from '@/common/debounceThrottle.js'
import {
  POSTOwndecomeBackApi,
  POSTOwndeLeaveYiwuApi,
  POSTRiskWEIxianApi,
  POSTDeclarationBayonet,
  POSTDeclaraCardeclare,
  POSTDeclaraShangyu,
  getMoreInfoBySign,
  getCardeclareTravel
} from '@/common/api/api.js'
import cookies from '../lib/util.cookie'
import {
  Toast
} from 'vant'
export default {
  name: 'newsuccess',
  props: {
    successInfo: {
      type: Object,
      default: () => null
    },
    successInfo_car: {
      type: Object,
      default: () => null
    },
    successShow: {
      type: Boolean,
      default: () => false
    },
    getRoute: {
      type: String,
      default: () => ''
    },
    addAdress: {
      type: String,
      default: () => ''
    },
    dataList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    dataList_text: {
      type: Object,
      default: () => {
        return {}
      }
    },
    fileList: {
      type: Array,
      default: () => {
        return []
      }
    },
    id_verify_result: {
      type: Number,
      default: () => {
        return 0
      }
    }
  },
  computed: {
    // showDiaglo: {
    //   get () {
    //     return this.successShow
    //   },
    //   set (val) {
    //     this.successShow = val
    //   }
    // }
  },
  watch: {
    dataList: {
      handler (newValue, oldValue) {
        this.formValue = newValue
        this.$forceUpdate()
      },
      deep: true
    },
    dataList_text: {
      handler (newValue, oldValue) {
        this.formValueText = newValue
        this.$forceUpdate()
      },
      deep: true
    }
  },
  created () {
    console.log('created')
    let showDigalogSession = sessionStorage.getItem('showDiaglo') || false
    if (!showDigalogSession) {
      if (this.successInfo != null) {
        console.log(this.successShow)
        this.showDiaglo = this.successShow
        sessionStorage.setItem('showDiaglo', this.showDiaglo)
        this.getCookieEndTime()
        this.localSuccessInfo = JSON.parse(JSON.stringify(this.successInfo))
        console.log('11111')
        this.timeDown()
      } else {
        this.showDiaglo = false
        console.log('22222')
      }
      if (this.getRoute === 'truckDeclaration') {
        if (this.successInfo_car !== null) {
          this.showDiaglo = this.successShow
          sessionStorage.setItem('showDiaglo', this.showDiaglo)
          let localSuccessInfoCar = cookies.get('successInfo_clareTravel')
          this.getCookieEndTime(localSuccessInfoCar)
          this.localSuccessInfo_car = JSON.parse(localSuccessInfoCar)
          console.log('11111c')
          this.timeDown()
        } else {
          this.showDiaglo = false
          console.log('22222c')
        }
      }
      console.log('showDiaglo', this.showDiaglo)
    }
  },
  data () {
    return {
      checked: false,
      password: '',
      formValue: this.dataList,
      formValueText: this.dataList_text,
      localSuccessInfo_car: {
        api_result: 0
      },
      successInfoDjs: '23:59:59',
      isDisabled: false,
      loadingRefersh: false,
      disabledRefresh: false,
      apiID: '',
      showDiaglo: false,
      timersNow: '',
      showInfo: true,
      localSuccessInfoEndTime: 0,
      timeOut: 0,
      getTravelTimeOut: '',
      localSuccessInfo: {
        real_name: '',
        phone: '',
        id_card: '',
        hsjc_24_info: '无',
        jkm: '',
        last_datetime: ''
      }
    }
  },
  destroyed () {
    clearTimeout(this.timersNow)
    clearTimeout(this.getTravelTimeOut)
  },
  methods: {
    changeCheckBox () {
      // this.isDisabled = !this.checked
    },
    getCookieEndTime () {
      console.log(this.getRoute)
      if (this.getRoute === 'truckDeclaration') {
        this.localSuccessInfoEndTime = cookies.get('successInfoEndTime_car')
        console.log(this.localSuccessInfoEndTime, 'localSuccessInfoEndTime')
      } else {
        this.localSuccessInfoEndTime = cookies.get('successInfoEndTime')
      }
    },
    timeDown () {
      if (this.showDiaglo) {
        // console.log(this.localSuccessInfoEndTime)
        let nowTime = Math.floor(new Date().getTime() / 1000)
        let endTime = parseInt(this.localSuccessInfoEndTime / 1000)
        let getTimeNumber = endTime - nowTime
        // console.log(nowTime, '现在时间', '结束时间=>', endTime, '剩余时间', endTime - nowTime)
        let hour = Math.floor(getTimeNumber / 3600)
        let minutes = Math.floor((getTimeNumber - hour * 3600) / 60)
        let seconds = Math.floor(getTimeNumber - (hour * 3600 + minutes * 60))
        // console.log(hour, minutes, seconds)
        this.successInfoDjs = `${this.formatNum(hour)}:${this.formatNum(minutes)}:${this.formatNum(seconds)}`
        // this.successInfoDjs = hour < 10 ? '0' + hour : hour + ':' + minutes < 10 ? '0' + minutes : minutes + seconds < 10 ? '0' + seconds : seconds

        if (nowTime > endTime) {
          this.showDiaglo = false
          clearTimeout(this.timersNow)
        } else {
          this.timersNow = setTimeout(() => {
            this.timeDown()
          }, 1000)
        }
      }
    },
    formatNum (num) {
      return num < 10 ? '0' + num : '' + num
    },
    back () {
      console.log('back')
      this.showDiaglo = false
      // this.goBack()
      this.$router.replace({
        path: '/'
      })
      // this.isDisabled = false
      // this.$emit('Back', false)
    },
    onSubmit () {
      this.isDisabled = true
      this.$emit('setSuccessInfoNull')
      const formValueResult = JSON.parse(JSON.stringify(this.dataList))
      formValueResult.city = this.dataList_text.city
      formValueResult.province = this.dataList_text.province
      formValueResult.county = this.dataList_text.county
      formValueResult.street = this.dataList_text.street
      formValueResult.yw_street = this.dataList_text.yw_street
      formValueResult.id_verify_result = this.id_verify_result
      switch (this.getRoute) {
        case 'risingFalling':
          POSTOwndeLeaveYiwuApi(formValueResult).then(res => {
            if (res.code !== 200) {
              this.isDisabled = false
              Toast.fail(res.msg)
            } else {
              this.showDiaglo = true
            }
          })
          break
        case 'goAndBackYiwu':
          POSTOwndecomeBackApi(formValueResult).then(res => {
            // 填写完毕清空缓存
            cookies.remove('backZanCunInfogoAndBackYiwu')
            this.onSubmitParams(res)
          })
          break
        case 'zizhushengbaoWeixian':
          POSTRiskWEIxianApi(formValueResult).then(res => {
            // 填写完毕清空缓存
            cookies.remove('backZanCunInfoWeixian')
            this.onSubmitParams(res)
          })
          break
        case 'declarationBayonet':
          delete formValueResult.id_verify_result
          delete formValueResult.province
          delete formValueResult.street
          delete formValueResult.county
          formValueResult.city = this.dataList.city
          formValueResult.barrier = 'barrier'
          formValueResult.district_id = this.dataList.district_id
          console.log('222222')
          POSTDeclarationBayonet(formValueResult).then(res => {
            this.onSubmitParams(res)
          })
          break
        case 'shangyu':
          delete formValueResult.id_verify_result
          delete formValueResult.province
          delete formValueResult.street
          delete formValueResult.county
          formValueResult.city = this.dataList.city
          formValueResult.barrier = 'barrier'
          console.log('222222')
          POSTDeclaraShangyu(formValueResult).then(res => {
            this.onSubmitParams(res)
          })
          break
        case 'truckDeclaration':

          POSTDeclaraCardeclare(formValueResult).then(res => {
            this.getTravel(res.data.id)
          })
          break
        default:
      }
    },
    getTravel (id) {
      const _this = this
      getCardeclareTravel({
        id
      }).then(res => {
        if (res.code !== 200) {
          this.isDisabled = false
        } else {
          this.apiID = id
          this.showDiaglo = true
          this.localSuccessInfo_car = res.data
          if (res.data.api_result || this.timeOut === 10) {
            console.log(this.showDiaglo)
            this.$forceUpdate()
            let seconds = 24 * 3600
            let expires = new Date(new Date() * 1 + seconds * 1000)
            console.log({ expires: expires }, 'expires')
            cookies.set('successInfo_clareTravel', JSON.stringify(res.data), { expires: expires })
            cookies.set('successInfoEndTime_car', expires.getTime(), { expires: expires })
            cookies.set('successInfoEndTime_Expires_car', expires, { expires: expires })
            this.getCookieEndTime()
            this.timeDown()
          } else {
            this.getTravelTimeOut = setTimeout(() => {
              _this.getTravel(_this.apiID)
              _this.timeOut++
            }, 1000)
          }
        }
      })
    },
    onSubmitParams (res) {
      if (res.code !== 200) {
        Toast.fail(res.msg)
        this.isDisabled = false
      } else {
        this.showDiaglo = true
        let seconds = 24 * 3600
        let expires = new Date(new Date() * 1 + seconds * 1000)
        console.log({ expires: expires }, 'expires')
        this.successInfo_yuanben = res.data
        cookies.set('successInfo', JSON.stringify(res.data), { expires: expires })
        cookies.set('successInfoEndTime', expires.getTime(), { expires: expires })
        cookies.set('successInfoEndTime_Expires', expires, { expires: expires })
        this.getCookieEndTime()
        this.localSuccessInfo = res.data
        this.timeDown()
      }
    },
    goBack () {
      this.$emit('closeSparingContent', false)
    },
    refersh () {
      this.loadingRefersh = true
      // 要执行的函数
      let successInfo = cookies.get('successInfo')
      let expires = cookies.get('successInfoEndTime_Expires')
      console.log(expires)
      if (typeof successInfo === 'string' && JSON.parse(successInfo)) {
        let successInfoList = JSON.parse(successInfo)
        console.log(successInfoList)
        getMoreInfoBySign({
          id_card: successInfoList.id_card,
          phone: successInfoList.phone,
          real_name: successInfoList.real_name,
          sign_out_time: successInfoList.sign_out_time,
          sign: successInfoList.sign
        }).then(res => {
          this.loadingRefersh = false
          console.log({ expires: new Date(expires) })
          // cookies.remove('successInfo')
          cookies.set('successInfo', JSON.stringify(res.data), { expires: new Date(expires) })
          this.disabledRefresh = true
          this.localSuccessInfo = ''
          this.localSuccessInfo = res.data
        })
      }
    }
  }
}
</script>

<style>
.van-cell__value--alone {
  text-align: right;
}
.van-cell__value {
  /* min-width: 70%; */
}
.sureSubmit {
  min-height: 100vh;
  background: #565353;
  width: 100%;
  padding: 0 15rem;
  box-sizing: border-box;
  padding-top: 1.3125rem;
  position: fixed;
  left: 0;
  top: 0;
}
.sureCellGroup {
  border-radius: 0.5rem !important;
  box-shadow: 0px 0px 0.5rem 1px rgba(12, 12, 12, 0.11);
  /* width: 22.1875rem; */
  /* width: ; */
  /* height: 18.75rem; */
  overflow-x: hidden;
  background: #ffff;
}
.submitBox {
  flex-direction: row;
  justify-content: center;
  display: flex;
  /* position: relative; */
  /* bottom: rem; */
  margin: 1rem 0;
  /* width: 22.5rem; */
}
.submitBoxBtn {
  height: 2.25rem !important;
  width: 22.5rem !important;
  border-radius: 0.25rem !important;
  color: #b33301 !important;
}
.submitBoxBtn_left {
  background: linear-gradient(-90deg, #fae4b3 0%, #faf7f1 100%) !important;
  border: none;
  margin-right: 1.25rem !important;
}
.submitBoxBtn_right {
  background: linear-gradient(-90deg, #fa5c00 0%, #fa9200 100%) !important;
  border: none;
}
.vanPopupIt {
