import cookies from '../lib/util.cookie'
import { Toast, Dialog } from 'vant'
import Cookies from 'js-cookie'

//
//

export const goBackAndSetCookies = {
  data () {
    return {

    }
  },
  mounted () {
    switch (this.routerName) {
      case 'zizhushengbaoWeixian':
        this.zizhushengbaoWeixianMounted()
        break
      case 'goAndBackYiwu':
        this.zizhushengbaoGoAndBackYiwu()
        break
      default:
        break
    }
  },
  methods: {
    zizhushengbaoWeixianMounted () {
      if (typeof cookies.get('backZanCunInfoWeixian') === 'string') {
        if (JSON.parse(cookies.get('backZanCunInfoWeixian'))) {
          let formValuteCookie = JSON.parse(cookies.get('backZanCunInfoWeixian'))
          this.getCookiesItems(formValuteCookie)
          this.deleteManyItems()
          delete this.formValute.arrival_transport_mode
          this.formValute_text.arrival_transport_mode = formValuteCookie.arrival_transport_mode_text
        }
      }
    },
    zizhushengbaoGoAndBackYiwu () {
      if (typeof cookies.get('backZanCunInfogoAndBackYiwu') === 'string') {
        if (JSON.parse(cookies.get('backZanCunInfogoAndBackYiwu'))) {
          let formValuteCookie = JSON.parse(cookies.get('backZanCunInfogoAndBackYiwu'))
          this.getCookiesItems(formValuteCookie)
          // this.deleteManyItems()
        }
      }
    },
    goBackSetCookies () {
      const _this = this
      const seconds = 24 * 3600
      const expires = new Date(new Date() * 1 + seconds * 1000)
      let formValute = {
      }
      Dialog.confirm({
        title: '提示',
        message: '是否保存当前所填写的内容'
      })
        .then(() => {
          console.log(_this.routerName)
          switch (_this.routerName) {
            case 'zizhushengbaoWeixian':
              console.log('_this.routerName zizhushengbaoWeixian')
              formValute = _this.setCookie(formValute, _this.formValute_text)
              formValute.arrival_transport_mode_text = _this.formValute_text.arrival_transport_mode
              formValute.yw_street = _this.formValute_text.yw_street
              cookies.remove('backZanCunInfoWeixian')
              cookies.set('backZanCunInfoWeixian', JSON.stringify(formValute), {
                expires: expires
              })
              break
            case 'goAndBackYiwu':
              console.log('_this.routerName goAndBackYiwu', _this.formValute_text)
              formValute = _this.setCookie(formValute, _this.formValute_text)
              console.log(formValute)
              cookies.remove('backZanCunInfogoAndBackYiwu')
              cookies.set('backZanCunInfogoAndBackYiwu', JSON.stringify(formValute), {
                expires: expires
              })
              break
            default:
              break
          }
          _this.goBack()
        })
        .catch(() => {
          // on cancel
          _this.goBack()
        })
    },
    goBack () {
      this.$router.push({
        path: '/'
      })
    },
    // 存储cookies
    setCookie (formValute, formValuteText) {
      formValute = this.formValute
      formValute.city = formValuteText.city
      formValute.county = formValuteText.county
      formValute.province = formValuteText.province
      formValute.street = formValuteText.street
      formValute.yw_street = formValuteText.yw_street
      return formValute
    },
    yanzhengUndefined (items) {
      if (typeof items === 'undefined') {
        return ''
      } else {
        return items
      }
    },
    // 删除多余属性
    deleteManyItems () {
      delete this.formValute.city
      delete this.formValute.county
      delete this.formValute.province
      delete this.formValute.street
      delete this.formValute.yw_street
    },
    // 获取cookies
    getCookiesItems (formValuteCookie) {
      console.log('formValuteCookie', formValuteCookie)
      this.formValute_text.city = this.yanzhengUndefined(formValuteCookie.city)
      this.formValute_text.county = this.yanzhengUndefined(formValuteCookie.county)
      this.formValute_text.province = this.yanzhengUndefined(formValuteCookie.province)
      this.formValute_text.street = this.yanzhengUndefined(formValuteCookie.street)
      this.formValute_text.yw_street = this.yanzhengUndefined(formValuteCookie.yw_street)
      this.formValute = formValuteCookie
    }
  }
}



