import { Toast } from 'vant'
import { getqgrkk, getAreaUserApi } from '../common/api/api'
import cookies from '../lib/util.cookie'
export const mixinsChaXun = {
  data () {
    return {
      overlay: false,
      show: false,
      id_verify_result: 1,
      isHide: false
    }
  },
  created () {
    console.log('created')
    let localSuccessInfoCar = cookies.get('successInfo_clareTravel')
    this.successInfoCar(localSuccessInfoCar)
    if (!localSuccessInfoCar) {
      this.successInfoSecc()
    }
  },
  methods: {
    successInfoCar (localSuccessInfoCar) {
      if (typeof localSuccessInfoCar === 'string') {
        if (!this.risingFallingType) {
          localSuccessInfoCar = JSON.parse(localSuccessInfoCar)
          console.log('successInfo_clareTravel', localSuccessInfoCar)
          if (localSuccessInfoCar) {
            let showDigalogSession = sessionStorage.getItem('showDiaglo') || false
            if (!showDigalogSession) {
              this.show = true
              this.successShow = true
            }
          } else {
            this.successInfo = null
          }
        }
      } else {
        this.successInfo = null
      }
    },
    successInfoSecc () {
      let localSuccessInfo = cookies.get('successInfo')
      if (typeof localSuccessInfo === 'string') {
        if (!this.risingFallingType) {
          localSuccessInfo = JSON.parse(localSuccessInfo)
          console.log('successInfo', localSuccessInfo)
          if (localSuccessInfo) {
            this.formValute.real_name = localSuccessInfo.real_name
            this.formValute.phone = localSuccessInfo.phone
            this.formValute.id_card = localSuccessInfo.id_card
            this.successInfo = localSuccessInfo
            let showDigalogSession = sessionStorage.getItem('showDiaglo') || false
            if (!showDigalogSession) {
              this.show = true
              this.successShow = true
            }
          } else {
            this.successInfo = null
          }
        }
      } else {
        this.successInfo = null
      }
    },
    // 若为40001状态码,则不通过,若为其他，均通过校验
    qgrkk (data) {
      this.overlay = true
      getqgrkk(
        data
      ).then(res => {
        this.overlay = false
        if (res.code !== 200) {
          if (res.code === 40001) {
            Toast.fail(res.msg)
          } else {
            this.show = true
            this.isHide = true
          }
          this.id_verify_result = 0
        } else {
          this.id_verify_result = 1
          this.show = true
          this.isHide = true
        }
      }).catch(() => {
        this.overlay = false
      })
    },
    // 图片大小校验
    onOversize (file) {
      console.log(file)
      Toast('文件大小不能超过 5MB')
    }
  }
}



